import React from "react";
import { NavLink } from "react-router-dom";


const Common = (props) => {
  return(<>
      <div id="header" className="d-flex align-item-center">
        <div className="container-fluid">
         <div className="row">
          <div className="col-10 mx-auto">
            <div className="row" id="mid">
               
               <div className="col-md-10 mt-4 pt-5 pt-lg-0 order-2 order-lg-1 ">
                 <h2>
                 {props.name} 
                 <strong className="navbar-brand">   Sohanur Rahman</strong>
                 </h2>
                 <h3 className="my-3">
                    You are talented Developer making website.
                 </h3>
                 <div className="mt-3">
                   <NavLink to={props.visit} className="btn btn-success">{props.btname}</NavLink>
                 </div>
               </div>
               <div className="col-lg-6 mt-2 order-1 order-lg-2 header-img">
               <img src={props.imgsrc} className="img-fluid animated" alt="home"/>
                 
               </div>
          </div>
          </div>
         </div>
    
        </div>
      </div>

    </>);
};
export default Common;