import React, { useState } from "react";
/*import { Link } from "react-router-dom";*/


const Contact = () => {

  const [data, setdata]=useState({
    name:"",
    phone:"",
    email:"",
    password:"",
  });

const Inputeven=(event)=>{
   const {name, value} =event.target;
   
   setdata((preValue)=>{
     return{
       ...preValue,
       [name]: value,
     } 
  });
};
const formSubmit =(e)=>{
  e.preventDefault();

  alert(`My name is ${data.name}  phone:${data.phone}  eamil:${data.email} and pass: ${data.password} `);

};

  return(<>
        <div className="container fluid my-5">
        <h1 className="text-center">Contact Us</h1>
        </div>
        <div className="container contact_info">
         <div className="row">
           <div className="col-md-6 col-10 mx-auto">
             <form onSubmit={formSubmit}>
             <div class="mb-3">
    <input type="text"
     class="form-control" 
     id="name" 
     name="name"
     value={data.name}
     onChange={Inputeven}
     placeholder="Enter your name"/>
  </div>
  <div class="mb-3">
    <input type="text"
     class="form-control"
      id="email"
      name="email"
      value={data.email}
      onChange={Inputeven} 
      placeholder="Email"/>
  </div>
  <div class="mb-3">
    <input type="number"
     class="form-control"
      id="phone"
      name="phone"
      value={data.phone}
      onChange={Inputeven} 
      placeholder="phone"/>
  </div>
  <div class="mb-3">
    <input type="password"
     class="form-control" 
     id="exampleInputPassword1"
     name="password"
     value={data.password}
     onChange={Inputeven}
      placeholder="password" />
  </div>
  
  <button type="submit" 
  class="btn btn-info"

  >Submit</button>

             </form>
  

           </div>
         </div>

        </div>
    </>);
};
export default Contact;